const JobTitle = ({ job }: { job: (string | JSX.Element)[] }) => {
	return (
		<Box pb={2}>
			{job.map((job, key) => (
				<div key={key}>
					<Box pb={key === 0 ? 2 : 0}>
						<Text fontSize={key === 0 ? '3xl' : key === 1 ? '2xl' : 'xl'}>
							<b>{job}</b>
						</Text>
					</Box>
					<Spacer />
				</div>
			))}
		</Box>
	);
};

const JobDescription = ({ description }: { description: JSX.Element[] }) => {
	return (
		<Flex direction={'column'} pb={2}>
			{description.map((description, key) => (
				<Box pb={4} key={key}>
					{description}
				</Box>
			))}
		</Flex>
	);
};

export const WorkExperience = (): JSX.Element => {
	return (
		<Flex pb={15} direction={'column'} pt={5} pl={15} pr={15} align="center" justifyContent="center" width="full">
			{Object.keys(WORK_EXP).map((key) => (
				<Box pb={2} key={key} mt={2} mb={2}>
					<JobTitle job={WORK_EXP[key].job} />
					<JobDescription description={WORK_EXP[key].description} />
				</Box>
			))}
		</Flex>
	);
};
