import * as React from 'react';
import { Box, Flex, Spinner, useMediaQuery } from '@chakra-ui/react';
import { Profile } from 'src/components/body/Profile';
import { WorkExperience } from 'src/components/body/WorkExperience';
import { Certifications } from 'src/components/body/Certifications';
import { Studies } from 'src/components/body/Studies';

export const Page = (): JSX.Element => {
	const [isDesktopView] = useMediaQuery('(min-width: 832px)');
	const [loaded, setLoaded] = React.useState<boolean>(false);
	const firstUpdate = React.useRef(true);
	React.useLayoutEffect(() => {
		if (firstUpdate.current) {
			firstUpdate.current = false;
			setLoaded(true);
		}
	});

	return (
		<Flex pt={10} direction="column" pr={isDesktopView ? 20 : 5} pl={isDesktopView ? 20 : 5}>
			<Box>
				{!loaded && (
					<Spinner thickness="10px" speed="0.65s" emptyColor="gray.500" color="gray.100" size={'xl'} />
				)}
				{loaded && (
					<Flex direction={'column'}>
						<Profile />
						<WorkExperience />
						<Certifications />
						<Studies />
					</Flex>
				)}
			</Box>
		</Flex>
	);
};
