import * as React from 'react';
import { Flex, Image, Box, Heading, useMediaQuery, useColorModeValue } from '@chakra-ui/react';
import profileImage from 'src/assets/logo.png';
import CV from 'src/assets/files/cv.pdf';
import { BaseLink } from 'src/components/generic/Link';

export const Profile = (): JSX.Element => {
	const [isDesktopView] = useMediaQuery('(min-width: 832px)');
	const imageRef = React.useRef<HTMLImageElement>(null);
	const [loaded, setLoaded] = React.useState(false);
	React.useEffect(() => {
		if (!loaded && imageRef.current?.complete) {
			setLoaded(true);
		}
	}, []);

	const bg = useColorModeValue('#1A202C', 'white');
	const color = useColorModeValue('white', '#1A202C');

	return (
		<Flex
			bg={bg}
			color={color}
			boxShadow="dark-lg"
			rounded="md"
			direction={isDesktopView ? 'row' : 'column'}
			alignItems="center"
			justify="space-around"
			pb={5}
			pt={5}
			fontSize={'2xl'}
		>
			<Image
				boxSize="200px"
				borderRadius="full"
				src={profileImage}
				alt={'Profile Image'}
				display={!loaded ? 'none' : 'block'}
				ref={imageRef}
				onLoad={() => {
					setLoaded(true);
				}}
			/>
			<Flex pt={!isDesktopView ? '10' : 'auto'} direction={'column'}>
				<Box pb={5}>
					<Heading fontSize={'5xl'}>
						<b>César Represa Bautista </b>
					</Heading>
				</Box>
				<Box>
					<Heading fontSize={'4xl'}>
						<b>Cloud Engineer</b>
					</Heading>
				</Box>
			</Flex>
			<Flex pt={!isDesktopView ? '10' : 'auto'} pb={!isDesktopView ? '10' : 'auto'} direction={'column'}>
				<Box pb={3}>
					<BaseLink href={'mailto:contact@cesarb.dev'} label={'contact@cesarb.dev'} bold={true} />
				</Box>
				<Box pb={3}>
					<BaseLink href={'tel:+31611417865'} label={'+31 611 417 865'} bold={true} />
				</Box>
				<Box>
					<BaseLink href={CV} label={'Curriculum Vitae'} bold={true} />
				</Box>
			</Flex>
		</Flex>
	);
};
